import React, { useContext, useState } from "react";
import Modal from "../universalComponents/modal/Modal";
import FormInput from "../universalComponents/form/FormInput";
import { Context } from "../../../..";

const DeleteUserModal = ({
  onHide = (f) => f,
  showModalWindowExecution = (f) => f,
}) => {
  const { users } = useContext(Context);
  const [email, setEmail] = useState("");

  const destroyUser = () => {
    const userExists = users.users.find((item) => item.email === email);

    if (userExists) {
      users.deleteUser(email);
      showModalWindowExecution(" !");
      setEmail("");
    } else {
      showModalWindowExecution("   email  !");
    }
  };

  return (
    <Modal
      title=" "
      action=""
      onClick={destroyUser}
      onHide={onHide}
    >
      <FormInput
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      />
    </Modal>
  );
};

export default DeleteUserModal;
