import React, { useContext, useState, useRef } from "react";
import { observer } from "mobx-react-lite";
import { Context } from "../../../../index";
import Dropdown from "../universalComponents/dropdown/Dropdown";
import Modal from "../universalComponents/modal/Modal";
import Button from "../universalComponents/Button";
import FormInput from "../universalComponents/form/FormInput";

const description =
  "       ,         (   ),   (          )     ,       :                 .\r\n\r\n ,    (    1926)      -   26  1928      . .   . . [1].\r\n\r\n     (. Visionette)  45-       Western Television  1929      100 [2].          ,           .               . -        ,  .  ,        ,     .         ,    .\r\n\r\n          ,     .        ,   1934   DFR (Deutscher Fernseh-Rundfunk    )      180- .            Telefunken[3].            : ,   .       30     445 ,         [4].         1929 ,  1  1938        120 [5].       1940 ,        .";

const CreateDevice = observer(
  ({ onHide = (f) => f, showModalWindowExecution = (f) => f }) => {
    const [selectedType, setSelectedType] = useState("");
    const [selectedBrand, setSelectedBrand] = useState("");
    const [name, setName] = useState("");
    const [price, setPrice] = useState("");
    const [file, setFile] = useState(null);
    const [fileName, setFileName] = useState("");
    const [desc, setDesc] = useState(null);
    const [descName, setDescName] = useState("");
    const [info, setInfo] = useState([]);

    const imageRef = useRef();
    const descRef = useRef();

    const { devices } = useContext(Context);

    const selectedFile = (e, condition) => {
      const file = e.target.files[0];

      if (condition === "image") {
        if (file === undefined) {
          if (fileName) {
            setFileName("");
          }
          return;
        }
        if (file.type !== "image/webp" && file.type !== "image/jpeg") {
          setFileName("   !");
          setTimeout(() => setFileName(""), 2000);
          return;
        }
        setFile(file);
        setFileName(file.name);
      } else if (condition === "desc") {
        if (file === undefined) {
          if (descName) {
            setDescName("");
          }
          return;
        }
        if (file.type !== "text/plain") {
          setDescName("   !");
          setTimeout(() => setDescName(""), 2000);
          return;
        }
        setDesc(file);
        setDescName(file.name);
      }
    };

    const addInfo = () => {
      setInfo([...info, { title: "", description: "", number: Date.now() }]);
    };

    const removeInfo = (number) => {
      setInfo(info.filter((i) => i.number !== number));
    };

    const changeInfo = (key, value, number) => {
      setInfo(
        info.map((i) => (i.number === number ? { ...i, [key]: value } : i))
      );
    };

    const addDevice = () => {
      if (selectedType && selectedBrand && name && price && file && desc) {
        const newDevice = {
          id: devices.devices[devices.devices.length - 1].id + 1,
          name,
          price,
          rating: 5,
          img: "funny_mobile.jpg",
          description,
          brandId: selectedBrand.id,
          typeId: selectedType.id,
          info,
        };

        devices.addDevice(newDevice);
        showModalWindowExecution(" !");
        setSelectedType("");
        setSelectedBrand("");
        setName("");
        setPrice("");
        setFile(null);
        setFileName("");
        setDesc(null);
        setDescName("");
        setInfo([]);
      } else {
        showModalWindowExecution("     !");
      }
    };

    return (
      <Modal
        title=" "
        action=""
        onClick={addDevice}
        onHide={onHide}
      >
        <Dropdown
          items={devices.types}
          nameToggle={selectedType.name || " "}
          selectItem={setSelectedType}
        />
        <Dropdown
          items={devices.brands}
          nameToggle={selectedBrand.name || " "}
          selectItem={setSelectedBrand}
        />

        <FormInput
          placeholder=" "
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
        <FormInput
          placeholder=" "
          type="number"
          value={price}
          onChange={(e) => setPrice(Number(e.target.value))}
        />
        <Button
          customStyle="store-create-device_button-addImg"
          variant="primary"
          onClick={() => imageRef.current.click()}
        >
            (*.webp, *.jpg)
        </Button>
        <div>{fileName}</div>
        <input
          type="file"
          ref={imageRef}
          style={{ display: "none" }}
          placeholder=""
          onChange={(e) => selectedFile(e, "image")}
        />
        <Button
          customStyle="store-create-device_button-addDesc"
          variant="primary"
          onClick={() => descRef.current.click()}
        >
             (*.txt)
        </Button>
        <div>{descName}</div>
        <input
          type="file"
          ref={descRef}
          style={{ display: "none" }}
          placeholder=""
          onChange={(e) => selectedFile(e, "desc")}
        />
        <hr />
        <Button
          customStyle="store-create-device_button-addInfo"
          variant="outline-dark"
          onClick={addInfo}
        >
           
        </Button>
        {info.map((i) => (
          <div className="store-create-device__info_item" key={i.number}>
            <FormInput
              value={i.title}
              onChange={(e) => changeInfo("title", e.target.value, i.number)}
              placeholder=""
            />
            <FormInput
              value={i.description}
              onChange={(e) =>
                changeInfo("description", e.target.value, i.number)
              }
              placeholder=""
            />
            <Button
              variant="outline-danger"
              onClick={() => removeInfo(i.number)}
            >
              
            </Button>
          </div>
        ))}
      </Modal>
    );
  }
);

export default CreateDevice;
